.App {
  width: 100%;
  overflow: hidden;
}

.App-header {
  background-color: #222;
  height: 20%;
  color: #f3fdff;
}

.App-thread {
  background-color: #eef3f6;
  margin-left: 33%;
}

.App-info {
  width: 33%;
  float: left;
}

h1 {
  margin: 0;
  padding: 2%;
  font-size: 1.3em;
}

h2 {
  color: #9eaab6;
  font-size: 1em;
  padding: 2%;
  top: 15%;
  position: relative;
  margin: 0;
}

ul {
  list-style-type: none;
  padding: 18px;
  bottom: 32px;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 2% 0 2%;
}

li {
  margin-top: 10px;
  margin-bottom: 10px;
  min-height: 50px;
}

.App-thread-container-avatar.left {
  float: left;
  margin-right: 10px;
  vertical-align: center;
}

.App-thread-container-avatarcontainer {
  float: left;
  width: 50px;
}

.App-thread-container-comment.left {
  background-color: #f3fdff;
  text-align: left;
  margin-left: 70px;
  color: #9eaab6;
  border-radius: 5px;
  padding: 5px 15px 5px 15px;
  -webkit-box-shadow: 0 1px 2px 0 #CCC, 0 1px 2px #CCC;
          box-shadow: 0 1px 2px 0 #CCC, 0 1px 2px #CCC;
}

.App-thread-container-avatarcontainer.right {
  float: right;
  width: 50px;
}

.App-thread-container-avatar.right {
  margin-left: 10px;
  float: left;
  vertical-align: center;
}

.App-thread-container-comment.right {
  text-align: left;
  word-wrap: break-word;
  margin-right: 70px;
  background-color: #f3fdff;
  color: #9eaab6;
  border-radius: 5px;
  padding: 5px 15px 5px 15px;
  -webkit-box-shadow: 0 1px 2px 0 #CCC, 0 1px 2px #CCC;
          box-shadow: 0 1px 2px 0 #CCC, 0 1px 2px #CCC;
}

.avatar {
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.App-info-filter-user {
  margin: 2%;
}

.filtered {
  opacity: 0.2;
}


